export default class ClienteModelo {
   dni: number;
   nombre: string;
   apellido: string;
   mail: string;
   celular: string;

   constructor() {
      this.dni = null;
      this.nombre = null;
      this.apellido = null;
      this.mail = null;
      this.celular = null;
   }
}



